
@import url('https://fonts.googleapis.com/css2?family=Orbitron:wght@400;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,700;1,400&display=swap');


/* start global ruls */

*, *::after, *::before {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    -webkit-tap-highlight-color: transparent;
}

body {
    font-family: 'Roboto', sans-serif;
}

sup {
    display: inline-block;
    text-align: center;
    margin-left: 5px;
    width: 15px;
    height: 15px;
    border-radius: 50%;
    background-color: #e6e6e6;
    color: #333;
    font-size: 0.7rem;
    cursor: help;
    user-select: none;
    transition: 0.3s;
    position: relative;
}

sup.active::before,
sup.active::after {
    display: inline-block;
}

sup::before {
    content: attr(data-title);
    position: absolute;
    bottom: 35px;
    right: -10px;
    width: 210px;
    height: 45px;
    padding: 10px;
    background-color: white;
    border-radius: 5px;
    box-shadow: 1px 4px 10px 0 #b3b3b3;
    font-size: 0.6rem;
    line-height: 1.4;
    display: none;
    animation: showTooltips 0.3s 1 forwards;
}

sup::after {
    content: '';
    position: absolute;
    border: 7px solid transparent;
    border-color: white transparent transparent transparent;
    bottom: 23px;
    left: 4px;
    display: none;
    animation: showTooltips 0.3s 1 forwards;
}

@keyframes showTooltips {
    from {
        transform: translateY(5px);
    }
    to {
        transform: translateY(0px);
    }
}
/* end global ruls */

/* start die */
.die__box {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 50px;
    height: 50px;
    border-radius: 10px;
    font-size: 1.2rem;
    font-weight: bold;
    box-shadow: 0px 4px 8px 0px #6363635c;
    cursor: pointer;
    user-select: none;
    transition: 0.3s;
}

.die__box:hover {
    transform: translateY(-3px);
}
/* end die */

/* start app */
.app {
    min-height: 100vh;
    padding: 20px;
    background-color: #5fa987;
    overflow: hidden;
}

.app__logo {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 20px;
    margin-bottom: 20px;

}

.app__logo > img {
    width: 100px;
}

.app__logo > p {
    font-size: 3rem;
    font-weight: bold;
    font-family: 'Orbitron', sans-serif;
    text-transform: uppercase;
    color: white;
    letter-spacing: 10px;
    text-align: center;
}

.app__container {
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-evenly;
    max-width: 1000px;
    margin: 0 auto;
    border-radius: 10px;
    background-color: white;
    box-shadow: 0 10px 20px 0px #49494996;
    position: relative;
}


.game__container {
    display: flex;
    flex-flow: column nowrap;
    justify-content: space-evenly;
    align-items: center;
    gap: 30px;
    padding: 20px;
    position: relative;
    flex-grow: 3;
}

.game__history {
    border-right: 1px solid #ececee;
    padding: 20px;
    flex-grow: 1;
    transition: 0.3s;
}

@media (max-width: 767px) {
    .app__container {
        flex-flow: column nowrap;
    }

    .game__history {
        border-right: unset;
        border-top: 1px solid #ececee;
        order: 2;
    }

    .app__logo > img {
    width: 80px;
    }

    .app__logo > p {
        font-size: 2.2rem;
    }

    .die__box:hover {
        transform: unset;
    }
}

.dies__container {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    gap:10px;
    max-width: 500px;
    position: relative;
}

.roll__btn {
    min-width: 80px;
    height: 30px;
    padding: 0 10px;
    border-radius: 3px;
    letter-spacing: 2px;
    border: none;
    background-color: #5C2921;
    color: white;
    font-weight: bold;
    cursor: pointer;
    transition: 0.3s;
}

.roll__btn:hover {
    background-color: #A8685E ;
}

.roll__btn:active {
    box-shadow: inset 2px 2px 3px 0px #2e2e2e;
}

.game__paragraph {
    text-transform: capitalize;
    max-width: 500px;
    line-height: 1.4;
}

.result {
    width: calc(100% + 20px);
    height: calc(100% + 20px);
    background-color: white;
    box-shadow: 0px 6px 7px 0px #7474747a;
    border: 1px solid #efefef;
    position: absolute;
    top: -10px;
    left: -10px;
    border-radius: 10px;
    display: flex;
    flex-flow: column nowrap;
    align-items: center;
    justify-content: space-evenly;
    gap: 10px;
    transition: 0.3s;
    padding: 20px;
    z-index: 100;
    user-select: none;
}

.result__text {
    letter-spacing: 2px;
}

.result__bar {
    display: flex;
    align-items: flex-end;
    gap: 10px;
    width: 100%;
}

.range__bar {
    width: 100%;
    height: 20px;
    background-color: #e2e2e2;
    border-radius: 5px;

}

.range__bar > span {
    display: block;
    border-radius: 5px 0 0 5px;
    height: inherit;
    width: 0;
    transition: 0.5s;
    animation: showBar 1s 1 forwards;
}

@keyframes showBar {
    from {
        width: 0;
    }
}

.daboor {
    color: #3e8263;
    font-weight: bold;
}

/* end app */

/* history card */

.history-card {
    background-color: white;
    border: 1px solid #ececec;
    border-radius: 10px;
    transition: 0.3s;
    padding: 10px;
    user-select: none;
    font-size: 0.7rem;
    margin: 10px 0;
}

.history-card > p {
    letter-spacing: 0;
    margin-bottom: 5px;
}

.history__bar {
    width: 100%;
    height: 10px;
    background-color: #e2e2e2;
    border-radius: 5px;
}

.history__bar > span {
    display: block;
    border-radius: 5px 0 0 5px;
    height: inherit;
    width: 0;
    transition: 0.5s;
}

.rest-btn {
    background-color: transparent;
    border: 1px solid #5fa987;
    color: #5fa987;
    font-weight: 400;
}

.rest-btn:hover {
    background-color: #5fa987;
    color: white;
}